@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700&display=swap);
:root{font-family:'Roboto', sans-serif;font-size:16px;--bg: #4d244e;--task: #6d335c;--task-hover: #ae5f75;--task-label: #d37b79;--task-text: #f6bd66;--nav-text: #aa8e8d;--task-completed: }*,:before,:after{box-sizing:border-box;margin:0px;padding:0px}body{background-color:#4d244e;background-color:var(--bg)}.App{display:flex;margin:40px auto 0px auto;flex-direction:column;width:100%;max-width:387px}.navbar{padding:25px}.navbar__heading{color:#aa8e8d;color:var(--nav-text)}.list{list-style-type:none;padding:25px}.list__item{font-weight:300;margin:1px 0px;background-color:#6d335c;background-color:var(--task);-webkit-transition:all ease-in-out 0.2s;transition:all ease-in-out 0.2s;padding:20px 10px;position:relative}.list__item:hover{background-color:#ae5f75;background-color:var(--task-hover)}.list__item--del{color:#d37b79;color:var(--task-label);font-weight:700;font-size:1.5rem;cursor:pointer;position:absolute;right:30px;top:25px}.list__item--del:hover{color:crimson}.list__item--label{color:#d37b79;color:var(--task-label);font-weight:700}.list__item--task{color:#f6bd66;color:var(--task-text)}.list__empty{padding-left:25px;color:#fff;font-size:1.2rem}.form{display:flex;flex-direction:column;padding:25px}.form__input{height:45px;margin-bottom:8px;padding:10px;background-color:#4d244e;background-color:var(--bg);color:#f6bd66;color:var(--task-text);border:2px solid #d37b79;border:2px solid var(--task-label)}.form__submit{border:0;height:35px;background-color:#f6bd66;background-color:var(--task-text);font-weight:700;font-size:1rem;-webkit-transition:all ease-in-out 0.2s;transition:all ease-in-out 0.2s}.form__submit:hover{color:#f6bd66;color:var(--task-text);background-color:#4d244e;background-color:var(--bg);border:2px solid #f6bd66;border:2px solid var(--task-text);cursor:pointer}

